import logo from "./logo.svg";
import "./App.css";
import SplitPane from "react-split-pane";
import { useEffect, useState } from "react";
import { GreetingsLeft, GreetingsRight } from "./Elements/Greetings";
import {
  Introduction,
  IntroductionLeft,
  IntroductionRight,
} from "./Elements/Introduction";
import { Motto } from "./Elements/Motto";
import { IntroductionDetail } from "./Elements/IntroductionDetail";

function App() {
  const [innerWidth, setInnerWidth] = useState(window.innerWidth);
  const [innerHeight, setInnerHeight] = useState(window.innerHeight);

  useEffect(() => {
    const resizeListener = () => {
      setInnerWidth(window.innerWidth);
      setInnerHeight(window.innerHeight);
    };
    window.addEventListener("resize", resizeListener);
  });

  console.log("innerWidth", innerWidth);
  console.log("innerWidth", innerHeight);
  return (
    <div>
      {/* <SplitPane split="vertical" minSize={100}>
        <div>right</div>
        <div>right</div>
      </SplitPane> */}
      <div style={{ display: "flex" }}>
        <div
          style={{
            backgroundColor: "#EF775D",
            width: innerWidth / 2,
            height: innerHeight,
          }}
        >
          <GreetingsRight />
        </div>
        <div style={{ width: innerWidth / 2, height: innerHeight }}>
          <GreetingsLeft />
        </div>
      </div>
      <div style={{ display: "flex" }}>
        <div style={{ width: innerWidth / 2, height: innerHeight }}>
          <IntroductionLeft />
        </div>
        <div style={{ width: innerWidth / 2, height: innerHeight }}>
          <IntroductionRight />
        </div>
      </div>
      <div style={{width: innerWidth, height: innerHeight }}>
            <IntroductionDetail/>
      </div>

      <div style={{width: innerWidth, height: innerHeight }}>
        <Motto/>
      </div>
    </div>
  );
}

export default App;
